import * as React from 'react';
import { Block, Plugin } from '@ali/4ever-cangjie';
import Group from '../components/group';

function createRenderNode(groupType: string): Plugin['renderNode'] {
  return function renderNode(props, controller, next) {
    const {
      node,
      attributes,
      isSelected,
      children,
      visible,
    } = props;

    if (Block.isBlock(node) && groupType === node.type) {
      return (
        <Group
          node={node as Block}
          attributes={attributes}
          controller={controller}
          isSelected={isSelected}
          visible={visible}
        >
          {children()}
        </Group>
      );
    }

    return next();
  };
}

export default createRenderNode;
